.Menu {
  background-color: white;
  width: 320px;
}

.headline {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  margin: 20px 30px 10px;
  border-bottom: 1px solid lightgrey;
  padding-bottom: 20px;
  text-transform: uppercase;
}

.headline img {
  width: 50px;
  margin-right: 10px;
}

.headline span {
  color: black;
  font-weight: 600;
  font-size: 24px;
  text-align: left;
}

.user-mgmt {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  margin: 10px 30px;
  border-bottom: 1px solid lightgrey;
}

.user-icon {
  color: grey;
  width: 50%;
  margin-bottom: 10px;
  padding: 10px 0;
}

.user-icon:hover {
  color: black;
}

.user-icon:first-of-type {
  border-right: 1px solid lightgrey;
}

.menu-items {
  margin: 10px 30px;
  border-bottom: 1px solid lightgrey;
  text-transform: uppercase;
}

.menu-items ul {
  margin: 0;
  padding: 0 0 20px;
}

.menu-items li {
  list-style-type: none;
  margin: 20px 0 0;
  font-weight: 600;
  font-size: 18px;
  cursor: pointer;
}

.menu-footer {
  position: absolute;
  bottom: 0;
  left: 150px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  color: grey;
}

.menu-footer:hover {
  color: black;
}

.menu-footer i {
  margin: 10px 0;
  font-size: 24px;
}

.menu-footer .tooltip-content {
  visibility: hidden;
  width: 500px;
  background-color: white;
  color: #333;
  text-align: center;
  border-radius: 6px;
  padding: 10px;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: -150px;
  box-shadow: 5px 5px 10px #333;
}

.menu-footer .tooltip-content::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 150px;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: white transparent transparent transparent;

}

.copyright {
    font-size: 13px;
}

.menu-footer:hover .tooltip-content {
  visibility: visible;
}

.menu-footer img {
    height: 60px;
    width: 180px;
    margin: 10px;
}