.ConfiguratorControls {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.models,
.navigation {
  align-self: flex-end;
  margin: 10px;
  display: flex;
  flex-wrap: nowrap;
}

.navigation-item,
.model-item {
  position: relative;
  display: inline-block;
}

.navigation-item .icon {
  font-size: 50px;
  margin: 10px 20px;
  color: white;
  text-shadow: 3px 3px 5px black;
}

.finish-configurator {
    width: 200px;
    margin-bottom: 20px;
    padding: 10px;
    background-color: white;
    color: black;
    border: 1px solid grey;
}

.navigation-item .tooltip-content,
.model-item .tooltip-content {
  visibility: hidden;
  width: 300px;
  background-color: white;
  color: #333;
  text-align: center;
  border-radius: 6px;
  padding: 10px;
  position: absolute;
  z-index: 1;
  bottom: 120%;
  left: 50%;
  margin-left: -160px;
  box-shadow: 5px 5px 10px #333;
}

.check {
  font-size: 30px;
  color: lightgreen;
}

.center {
    position: absolute;
    z-index: 1;
    bottom: 50%;
    left: 50%;
    margin-left: -15px;
    margin-bottom: -12px;
}

.navigation-item .tooltip-content::after,
.model-item .tooltip-content::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: white transparent transparent transparent;
}

.navigation-item:hover .tooltip-content,
.model-item:hover .tooltip-content {
  visibility: visible;
}

.models {
  display: flex;
  flex-wrap: nowrap;
}

.models img {
  padding: 10px;
  background-color: white;
  height: 50px;
  width: 50px;
  box-shadow: 3px 3px 10px black;
  margin: 0 20px;
}

.underline {
    text-decoration: underline;
}